import React from 'react';
import Card from '@mui/material/Card';
import {Checkbox, Grid} from '@mui/material';
import InfoOutlinedIcon from '@mui/icons-material/InfoOutlined';
import FavoriteOutlinedIcon from '@mui/icons-material/InfoOutlined';
import Rating from '@mui/lab/Rating';
import Slider from 'react-slick';
import IntlMessages from '../../../../../@crema/utility/IntlMessages';
import Box from '@mui/material/Box';
import PropTypes from 'prop-types';
import {green} from '@mui/material/colors';
import {Fonts} from '../../../../../shared/constants/AppEnums';
import {useDispatch} from 'react-redux';
import {useNavigate} from 'react-router-dom';
import {setCurrentProduct} from '../../../../../redux/actions';
import MediaSlider from './MediaSlider';
import {useEffect, useState} from 'react';
const settings = {
  dots: true,
  arrows: false,
  infinite: true,
  speed: 500,
  autoplay: true,
  slidesToShow: 1,
  slidesToScroll: 1,
};

const ListItem = (props) => {
  const {item} = props;
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const [avarage, setavarage] = useState(0);

  useEffect(() => {
    var sumOfRev = 0;
    item.reviews?.map((review) => {
      if (review.rating) {
        sumOfRev += review.rating;
      }
    });

    setavarage(sumOfRev / item.review);
  }, [item]);

  return (
    <Card
      sx={{
        p: 5,
        mb: 4,
        cursor: 'pointer',
      }}
      className='item-hover'
      onClick={() => {
        dispatch(setCurrentProduct(item));
        navigate('/product-management/product_detail/' + item.id, {
          state: item,
        });
      }}
    >
      <Box
        sx={{
          display: 'flex',
          flexDirection: {xs: 'column', sm: 'row'},
        }}
      >
        <Box
          sx={{
            pr: {xs: 0, sm: 4},
            mb: {xs: 3, sm: 0},
            maxWidth: '100%',
            textAlign: 'center',
            width: {sm: '8rem', xl: '10rem'},
          }}
        >
          <MediaSlider>
            <Slider {...settings}>
              {item.sm_pictures
.map((img) => {
                return (
                  <Box
                    sx={{
                      mb: 2,
                      textAlign: 'center',
                      maxHeight: 140,
                      minHeight: 140,
                      position: 'relative',
                      '& img': {
                        maxHeight: '100%',
                        maxWidth: '100%',
                      },
                    }}
                    key={img.url}
                  >
                    <Box
                      sx={{
                        position: 'absolute',
                        width: '100%',
                        height: '100%',
                      }}
                    >
                      <img src={img.url} alt='watch' />
                    </Box>
                  </Box>
                );
              })}
            </Slider>
          </MediaSlider>
        </Box>

        <Box
          sx={{
            width: {
              xs: '100%',
              sm: 'calc(100% - 8rem)',
              xl: 'calc(100% - 10rem)',
            },
            display: 'flex',
            flexDirection: 'column',
          }}
        >
          <Box
            sx={{
              display: 'flex',
              alignItems: 'center',
              mb: 1,
              mt: -4,
            }}
          >
            <Box
              component='h3'
              sx={{
                fontWeight: Fonts.BOLD,
                fontSize: 16,
                overflow: 'hidden',
                textOverflow: 'ellipsis',
                whiteSpace: 'nowrap',
                width: 'calc(100% - 110px)',
              }}
            >
              {item.name}
            </Box>
            {item.sale_price ? (
              <>
                <Box
                  sx={{
                    mb: 1,
                    fontWeight: Fonts.MEDIUM,
                    color: green[600],
                  }}
                >
                   {item.sale_price
                  ? (((item.price - item.sale_price) / item.price) * 100).toFixed(1)
                  : 0}
                 % <IntlMessages id='ecommerce.descont' />
                </Box>
              </>
            ) : (
              <></>
            )}

            <Box
              component='span'
              sx={{
                ml: 'auto',
                display: 'block',
                textAlign: 'right',
                '& .MuiCheckbox-root': {
                  padding: 3,
                },
              }}
            >
              <Checkbox
                icon={<InfoOutlinedIcon />}
                checkedIcon={<FavoriteOutlinedIcon />}
              />
            </Box>
          </Box>

          <Box
            component='p'
            sx={{
              color: 'text.secondary',
              mb: {xs: 3, xl: 5},
              fontSize: 14,
            }}
          >
            {item.short_desc}
          </Box>
          <Grid container>
            <Grid item xs={4}>
              <Box
                sx={{
                  mb: {xs: 3, xl: 5},
                  display: 'flex',
                  flexWrap: 'wrap',
                  fontSize: {xs: 12, xl: 14},
                }}
              >
                <Box
                  sx={{
                    mr: {xs: 2, xl: 4},
                    mb: 1,
                    pr: {xs: 2, xl: 4},
                  }}
                >
                  <Box
                    component='span'
                    sx={{
                      color: 'text.secondary',
                      fontWeight: Fonts.MEDIUM,
                      textTransform: 'uppercase',
                    }}
                  >
                    <IntlMessages id='ecommerce.exclusivePrice' />:
                  </Box>
                  <Box
                    component='span'
                    sx={{
                      ml: 2,
                      fontWeight: Fonts.MEDIUM,
                    }}
                  >
                    {item.sale_price ? item.sale_price : item.price}
                  </Box>
                </Box>
              </Box>
              {item.sale_price ? (
                <Box
                  sx={{
                    mb: {xs: 3, xl: 5},
                    display: 'flex',
                    flexWrap: 'wrap',
                    fontSize: {xs: 12, xl: 14},
                  }}
                >
                  {item.sale_price ? (
                    <>
                      <Box
                        sx={{
                          mr: {xs: 2, xl: 4},
                          mb: 1,
                          pr: {xs: 2, xl: 4},
                          color: 'text.secondary',
                          fontWeight: Fonts.MEDIUM,
                        }}
                      >
                        <IntlMessages id='ecommerce.mrp' /> :
                        <Box
                          component='span'
                          sx={{
                            textDecoration: 'line-through',
                            color: '#cc0f02',
                          }}
                        >
                          {item.price}
                        </Box>
                      </Box>
                    </>
                  ) : (
                    <></>
                  )}
                </Box>
              ) : (
                <></>
              )}
              <Box
                sx={{
                  mb: {xs: 3, xl: 5},
                  display: 'flex',
                  flexWrap: 'wrap',
                  fontSize: {xs: 12, xl: 14},
                }}
              >
                <Box
                  sx={{
                    mr: {xs: 2, xl: 4},
                    mb: 1,
                    pr: {xs: 2, xl: 4},
                  }}
                >
                  <Box
                    component='span'
                    sx={{
                      color: 'text.secondary',
                      fontWeight: Fonts.MEDIUM,
                    }}
                  >
                    <IntlMessages id='ecommerce.cost' />:
                  </Box>
                  <Box
                    component='span'
                    sx={{
                      ml: 2,
                      fontWeight: Fonts.MEDIUM,
                    }}
                  >
                    {item.cost}
                  </Box>
                </Box>
              </Box>
            </Grid>

            <Grid item xs={3}>
              <Box
                sx={{
                  mb: {xs: 3, xl: 5},
                  display: 'flex',
                  flexWrap: 'wrap',
                  fontSize: {xs: 12, xl: 14},
                }}
              >
                <Box
                  sx={{
                    mr: {xs: 2, xl: 4},
                    mb: 1,
                    pr: {xs: 2, xl: 4},
                  }}
                >
                  <Box
                    component='span'
                    sx={{
                      color: 'text.secondary',
                      fontWeight: Fonts.MEDIUM,
                      textTransform: 'uppercase',
                    }}
                  >
                    <IntlMessages id='ecommerce.stock' />:
                  </Box>
                  <Box
                    component='span'
                    sx={{
                      ml: 2,
                      fontWeight: Fonts.MEDIUM,
                    }}
                  >
                    {item.stock}
                  </Box>
                </Box>
              </Box>
              <Box
                sx={{
                  mb: {xs: 3, xl: 5},
                  display: 'flex',
                  flexWrap: 'wrap',
                  fontSize: {xs: 12, xl: 14},
                }}
              >
                <Box
                  sx={{
                    mr: {xs: 2, xl: 4},
                    mb: 1,
                    pr: {xs: 2, xl: 4},
                  }}
                >
                  <Box
                    component='span'
                    sx={{
                      color: 'text.secondary',
                      fontWeight: Fonts.MEDIUM,
                    }}
                  >
                    <IntlMessages id='ecommerce.sold' />:
                  </Box>
                  <Box
                    component='span'
                    sx={{
                      ml: 2,
                      fontWeight: Fonts.MEDIUM,
                    }}
                  >
                    {item.sold}
                  </Box>
                </Box>
              </Box>
              <Box
                sx={{
                  mb: {xs: 3, xl: 5},
                  display: 'flex',
                  flexWrap: 'wrap',
                  fontSize: {xs: 12, xl: 14},
                }}
              >
                <Box
                  sx={{
                    mr: {xs: 2, xl: 4},
                    mb: 1,
                    pr: {xs: 2, xl: 4},
                  }}
                >
                  <Box
                    component='span'
                    sx={{
                      color: 'text.secondary',
                      fontWeight: Fonts.MEDIUM,
                    }}
                  >
                    <IntlMessages id='ecommerce.dameged' />:
                  </Box>
                  <Box
                    component='span'
                    sx={{
                      ml: 2,
                      fontWeight: Fonts.MEDIUM,
                    }}
                  >
                    {item.damegdCouont}
                  </Box>
                </Box>
              </Box>
            </Grid>
            <Grid item xs={5}>
              {item.until ? (
                <Box
                  sx={{
                    mb: {xs: 3, xl: 5},
                    display: 'flex',
                    flexWrap: 'wrap',
                    fontSize: {xs: 12, xl: 14},
                  }}
                >
                  <Box
                    sx={{
                      mr: {xs: 2, xl: 4},
                      mb: 1,
                      pr: {xs: 2, xl: 4},
                    }}
                  >
                    <Box
                      component='span'
                      sx={{
                        color: 'text.secondary',
                        fontWeight: Fonts.MEDIUM,
                        textTransform: 'uppercase',
                      }}
                    >
                      <IntlMessages id='ecommerce.clint_sale' />:
                    </Box>
                    <Box
                      component='span'
                      sx={{
                        ml: 2,
                        fontWeight: Fonts.MEDIUM,
                      }}
                    >
                      {item.until}
                    </Box>
                  </Box>
                </Box>
              ) : (
                <></>
              )}
              {item.until_vendor ? (
                <>
                  <Box
                    sx={{
                      mb: {xs: 3, xl: 5},
                      display: 'flex',
                      flexWrap: 'wrap',
                      fontSize: {xs: 12, xl: 14},
                    }}
                  >
                    <Box
                      sx={{
                        mr: {xs: 2, xl: 4},
                        mb: 1,
                        pr: {xs: 2, xl: 4},
                      }}
                    >
                      <Box
                        component='span'
                        sx={{
                          color: 'text.secondary',
                          fontWeight: Fonts.MEDIUM,
                        }}
                      >
                        <IntlMessages id='ecommerce.vendor_sale' />:
                      </Box>
                      <Box
                        component='span'
                        sx={{
                          ml: 2,
                          fontWeight: Fonts.MEDIUM,
                        }}
                      >
                        {item.until_vendor}
                      </Box>
                    </Box>
                  </Box>
                </>
              ) : (
                <></>
              )}
            </Grid>
          </Grid>
          <Box
            sx={{
              mt: 'auto',
              mb: -3,
              mx: {xs: -2, xl: -3},
              display: 'flex',
              flexWrap: 'wrap',
              justifyContent: 'space-between',
              alignItems: 'center',
            }}
          >
            <Box
              sx={{
                px: {xs: 2, xl: 3},
                mb: 2,
                display: 'flex',
                alignItems: 'center',
              }}
            >
              <Box sx={{mt: 1.5}}>
                <Rating
                  size='small'
                  value={Number(avarage).toFixed(1)}
                  readOnly
                />
              </Box>
              <Box
                sx={{
                  ml: 2,
                }}
              >
                {item.review}
              </Box>
            </Box>
          </Box>
        </Box>
      </Box>
    </Card>
  );
};

export default ListItem;

ListItem.propTypes = {
  item: PropTypes.object.isRequired,
};
