import React from 'react';
import Card from '@mui/material/Card';
import StarBorderIcon from '@mui/icons-material/StarBorder';
import InfoOutlinedIcon from '@mui/icons-material/InfoOutlined';
import FavoriteOutlinedIcon from '@mui/icons-material/InfoOutlined';
import {Checkbox} from '@mui/material';
import IntlMessages from '../../../../../@crema/utility/IntlMessages';
import Box from '@mui/material/Box';
import PropTypes from 'prop-types';
import {green} from '@mui/material/colors';
import {Fonts} from '../../../../../shared/constants/AppEnums';
import {useDispatch} from 'react-redux';
import {useNavigate} from 'react-router-dom';
import {getProductDetail} from '../../../../../redux/actions';
import {useEffect, useState} from 'react';

const GridItem = (props) => {
  const {item} = props;
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const [avarage, setavarage] = useState(0);

  useEffect(() => {
    var sumOfRev = 0;
    item.reviews?.map((review) => {
      if (review.rating) {
        sumOfRev += review.rating;
      }
    });

    setavarage(sumOfRev / item.review);
  }, [item]);
  return (
    <Card
      sx={{
        p: 5,
        cursor: 'pointer',
        height: '100%',
        display: 'flex',
        flexDirection: 'column',
      }}
      className='item-hover'
      onClick={() => {
        dispatch(getProductDetail(item.id));
        navigate('/product-management/product_detail/' + item.id, {
          state: item,
        });
      }}
    >
      <Box
        sx={{
          mt: 2,
          mb: 5,
          display: 'flex',
          justifyContent: 'space-between',
        }}
      >
        {avarage ? (
          <Box
            component='span'
            sx={{
              maxHeight: 28,
              width: 48,
              color: 'primary.contrastText',
              backgroundColor: green[500],
              pt: 1.5,
              pb: 1,
              ml: 1,
              px: 2,
              display: 'flex',
              justifyContent: 'center',
              alignItems: 'center',
              fontWeight: Fonts.MEDIUM,
              borderRadius: 8,
              fontSize: 14,
            }}
          >
            <>
              <Box component='span' sx={{pb: 1.25}}>
                {avarage.toFixed(1)}
              </Box>
              <Box
                component='span'
                sx={{
                  ml: 1,
                }}
              >
                <StarBorderIcon
                  sx={{
                    fontSize: 16,
                  }}
                />
              </Box>
            </>
          </Box>
        ) : (
          <></>
        )}
        <Box
          sx={{
            mx: 2,
            maxHeight: {xs: 140, sm: 200, md: 260},
            minHeight: {xs: 140, sm: 200, md: 260},
            display: 'flex',
            flexDirection: 'column',
            alignItems: 'center',
            justifyContent: 'center',
            '& img': {
              maxHeight: '100%',
              maxWidth: '100%',
            },
          }}
        >
          {<img src={item.sm_pictures[0].url} alt='watch' />}
        </Box>
        <Box
          sx={{
            mt: -3,
          }}
        >
         <Checkbox
                icon={<InfoOutlinedIcon />}
                checkedIcon={<FavoriteOutlinedIcon />}
              />
        </Box>
      </Box>

      <Box
        sx={{
          mb: 1,
          color: 'text.primary',
          fontWeight: Fonts.BOLD,
          fontSize: 16,
          overflow: 'hidden',
          textOverflow: 'ellipsis',
          whiteSpace: 'nowrap',
        }}
        component='h3'
      >
        {item.name}
      </Box>

      <Box
        component='p'
        sx={{
          mb: 3,
          color: 'text.secondary',
          fontSize: 14,
          overflow: 'hidden',
          textOverflow: 'ellipsis',
          whiteSpace: 'nowrap',
        }}
      >
        {item.short_desc}
      </Box>

      <Box
        sx={{
          mx: -1,
          mt: 'auto',
          display: 'flex',
          alignItems: 'center',
          fontWeight: Fonts.MEDIUM,
          justifyContent: 'space-between',
          fontSize: {xs: 12, xl: 14},
        }}
      >
        <Box>
          <Box
            component='span'
            sx={{
              px: 1,
              mb: 2,
              color: 'text.primary',
            }}
          >
            ${item.sale_price ? item.sale_price : item.price}{' '}
          </Box>
          {item.sale_price ? (
            <>
              <Box
                component='span'
                sx={{
                  px: 1,
                  mb: 2,
                  color: '#cc0f02',
                  textDecoration: 'line-through',
                }}
              >
                ${item.price}
              </Box>
              <Box
                component='span'
                sx={{
                  px: 1,
                  mb: 2,
                  color: green[500],
                }}
              >
                {item.sale_price
                  ? (((item.price - item.sale_price) / item.price) * 100).toFixed(1)
                  : 0}
                % <IntlMessages id='ecommerce.descont' />
              </Box>
            </>
          ) : (
            <></>
          )}
        </Box>
        
      </Box>
    </Card>
  );
};

export default GridItem;

GridItem.propTypes = {
  item: PropTypes.object.isRequired,
};
