import React, {useEffect, useState} from 'react';
// import ProductsCategory from './ProductsCategory';
import {Box} from '@mui/material';
import {Fonts} from '../../../../shared/constants/AppEnums';
import Divider from '@mui/material/Divider';
import PriceSelector from './PriceSelector';
import AppScrollbar from '../../../../@crema/core/AppScrollbar';
import AppList from '../../../../@crema/core/AppList';
import CheckedCell from './CheckedCell';
import {
  DiscountList,
  ProductColors,
} from '../../../../@crema/services/db/ecommerce/ecommerceData';
import AppGrid from '../../../../@crema/core/AppGrid';
import ColorCell from './ColorCell';
// import RatingCell from './RatingCell';
import {useDispatch, useSelector} from 'react-redux';
import {setFilters} from '../../../../redux/actions';
import {GET_BRAND, GET_CATEGORY} from '../../../../query/Prodact/product';
import {useQuery} from '@apollo/client';

const ProductSidebar = () => {
  const {data: catDATA, loading: catLoading} = useQuery(GET_CATEGORY);
  const {data: BrandData, loading} = useQuery(GET_BRAND);
  useEffect(() => {
    if (BrandData) console.log(BrandData);
    if (catDATA) console.log(catDATA);
  }, [BrandData, catDATA]);
  const dispatch = useDispatch();
  const filterData = useSelector(({ecommerce}) => ecommerce.filterData);
  const [selectedBrand, setSelectedBrand] = useState(filterData.brand);
  const [selectedFor, setSelectedFor] = useState(filterData.ideaFor);
  const [selectedDiscount, setSelectedDiscount] = useState(filterData.discount);
  const [selectedColor, setSelectedColor] = useState(filterData.color);
  // const [customerRating, setCustomerRating] = useState(filterData.rating);
  const [selectedColorName, setSelectedColorName] = useState(filterData.color);
  const [value, setValue] = React.useState([0, 10000]);
  useEffect(() => {
    setTimeout(() => {
      dispatch(
        setFilters({
          title: filterData.title,
          brand: selectedBrand,
          ideaFor: selectedFor,
          discount: selectedDiscount,
          color: selectedColorName,
          rating: value,
        }),
      );
    }, 500);
  }, [
    dispatch,
    filterData.title,
    selectedBrand,
    selectedFor,
    selectedDiscount,
    selectedColor,
    value,
  ]);

  const onSelectBrand = (brandId) => {
    if (selectedBrand.some((brand) => brand === brandId)) {
      setSelectedBrand(selectedBrand.filter((brand) => brand !== brandId));
    } else {
      setSelectedBrand(selectedBrand.concat(brandId));
    }
  };

  const onSelectFor = (id) => {
    if (selectedFor.some((item) => item === id)) {
      setSelectedFor(selectedFor.filter((item) => item !== id));
    } else {
      setSelectedFor(selectedFor.concat(id));
    }
  };

  const onSelectDiscount = (id) => {
    if (selectedDiscount.some((item) => item === id)) {
      setSelectedDiscount(selectedDiscount.filter((item) => item !== id));
    } else {
      setSelectedDiscount(selectedDiscount.concat(id));
    }
  };

  const onSelectColor = (id) => {
    let x = ProductColors.find((item) => item.color == id);

    if (selectedColorName.some((item) => item === x.color_name)) {
      setSelectedColorName(
        selectedColor.filter((item) => item !== x.color_name),
      );
    } else {
      setSelectedColorName(selectedColor.concat(x.color_name));
    }

    if (selectedColor.some((item) => item === id)) {
      setSelectedColor(selectedColor.filter((item) => item !== id));
    } else {
      setSelectedColor(selectedColor.concat(id));
    }
  };
  useEffect(() => {
    console.log(value);
  }, [value]);
  // const onSelectRating = (id) => {
  //   if (customerRating.some((item) => item === id)) {
  //     setCustomerRating(customerRating.filter((item) => item !== id));
  //   } else {
  //     setCustomerRating(customerRating.concat(id));
  //   }
  // };

  return (
    <>
      {' '}
      {loading && !BrandData ? (
        ''
      ) : catLoading && !catDATA ? (
        ''
      ) : (
        <AppScrollbar>
          <Box
            sx={{
              p: 6,
            }}
          >
            <Box
              component='h5'
              sx={{
                mb: 2,
                fontWeight: Fonts.MEDIUM,
              }}
            >
              Filter By
            </Box>
            {/* <Box
          sx={{
            color: 'text.secondary',
            mb: 4,
            fontWeight: Fonts.MEDIUM,
          }}
        >
          CATEGORIES
        </Box>
        <ProductsCategory />
        <Divider
          sx={{
            mt: 4,
          }}
        /> */}
            <Box
              sx={{
                color: 'text.secondary',
                my: 4,
                fontWeight: Fonts.MEDIUM,
              }}
            >
              PRICE
            </Box>
            <PriceSelector setValuee={setValue} />
            <Divider
              sx={{
                mt: 4,
              }}
            />
            <Box
              sx={{
                color: 'text.secondary',
                my: 4,
                fontWeight: Fonts.MEDIUM,
              }}
            >
              Category
              <AppList
                data={catDATA.category}
                renderRow={(data) => (
                  <CheckedCell
                    key={data.slug}
                    data={data}
                    onChange={onSelectFor}
                    selected={selectedFor}
                  />
                )}
              />
            </Box>
            <Divider
              sx={{
                mt: 4,
              }}
            />

            <Box
              sx={{
                color: 'text.secondary',
                my: 4,
                fontWeight: Fonts.MEDIUM,
              }}
            >
              BRAND
              <AppList
                data={BrandData.brand}
                renderRow={(data) => (
                  <CheckedCell
                    key={data.slug}
                    data={data}
                    onChange={onSelectBrand}
                    selected={selectedBrand}
                  />
                )}
              />
            </Box>
            <Divider
              sx={{
                mt: 4,
              }}
            />

            <Box
              sx={{
                color: 'text.secondary',
                my: 4,
                fontWeight: Fonts.MEDIUM,
              }}
            >
              Size
              <AppList
                data={DiscountList}
                renderRow={(data) => (
                  <CheckedCell
                    key={data.id}
                    data={data}
                    onChange={onSelectDiscount}
                    selected={selectedDiscount}
                  />
                )}
              />
            </Box>
            <Divider
              sx={{
                mt: 4,
              }}
            />
            <Box
              sx={{
                color: 'text.secondary',
                my: 4,
                fontWeight: Fonts.MEDIUM,
              }}
            >
              <Box sx={{mb: 3}}>COLOR</Box>

              <AppGrid
                data={ProductColors}
                column={6}
                itemPadding={10}
                renderRow={(data, index) => (
                  <ColorCell
                    key={'color-' + index}
                    data={data.color}
                    selected={selectedColor}
                    onChange={onSelectColor}
                  />
                )}
              />
            </Box>
            <Divider
              sx={{
                mt: 4,
              }}
            />
            {/* <Box
          sx={{
            color: 'text.secondary',
            my: 4,
            fontWeight: Fonts.MEDIUM,
          }}
        >
          CUSTOMER RATINGS
          <AppList
            data={[5, 4, 3, 2, 1]}
            renderRow={(data) => (
              <RatingCell
                key={data}
                data={data}
                onChange={onSelectRating}
                selected={customerRating}
              />
            )} */}
            {/* /> */}
            {/* </Box> */}
          </Box>
        </AppScrollbar>
      )}{' '}
    </>
  );
};

export default ProductSidebar;
