import React, { useEffect, useState } from 'react';
import AppsHeader from '../../../../@crema/core/AppsContainer/AppsHeader';
import ProductHeader from '../ProductHeader';
import { useDispatch, useSelector } from 'react-redux';
import { VIEW_TYPE } from '../../../../redux/reducers/Ecommerce';
import ProductGrid from './ProductGrid/index';
import ProductList from './ProductList';
import AppsContent from '../../../../@crema/core/AppsContainer/AppsContent';
import { alpha, Box, Hidden } from '@mui/material';
import { useThemeContext } from '../../../../@crema/utility/AppContextProvider/ThemeContextProvider';
import { onGetEcommerceData, searchFilter } from '../../../../redux/actions';
import AppsFooter from '../../../../@crema/core/AppsContainer/AppsFooter';
import AppsPagination from '../../../../@crema/core/AppsPagination';
import { useLazyQuery } from '@apollo/client';
import { GET_PRODUCTS } from '../../../../query/Prodact/product';

const ProductListing = () => {
  const dispatch = useDispatch();
  const { theme } = useThemeContext();
  const [page, setPage] = useState(0);
  // const [products,setProducts]=useState([]);

  const ecommerceList = useSelector(({ ecommerce }) => ecommerce.ecommerceList);
  const { list = [], sum = 0, lastVisible } = ecommerceList;
  const [datalist ,setDatalist]=useState(list);
  const viewType = useSelector(({ ecommerce }) => ecommerce.viewType);
  const filterData = useSelector(({ ecommerce }) => ecommerce.filterData);
  const loading = useSelector(({ common }) => common.loading);
  const [index, setIndex] = useState('');
  const [isNext, setIsNext] = useState(false);
  const [search, setSearchQuery] = useState('');
  

  const [getProducts, { data }] = useLazyQuery(GET_PRODUCTS, { fetchPolicy: 'no-cache' });
  const [firstLoading, setFirstLoading] = useState(true);

  const [perPage] = useState(10);
  const [lastEndix, setLastEndix] = useState('');
  // const[pages,setPages]=useState(1);
  // const [search, setSearchQuery] = useState('');
  useEffect(() => {
console.log(filterData);
   
    dispatch(onGetEcommerceData({ ...filterData, page, index, isNext }));
    // setProducts(list);

  }, [dispatch, page]);
useEffect(()=>{setDatalist(list);},[list]);
  const onPageChange = (event, value) => {
    if (value > page) {
      setIsNext(true);
    } else {
      setIsNext(false);
    }
    setPage(value);
    setIndex(lastVisible);
  };

  const searchProduct = (title) => {
    // setSearchQuery(title);
    setSearchQuery(title);

  };
  useEffect(() => {

    if (search.length > 2) {
      setTimeout(() => {
        getProducts({
          variables: {
            searchTerm: search,
            color: [],
            size: [],
            brand: [],
            page: firstLoading ? 1 : page,
            perPage: perPage,
            list: true,
            lastEndix: lastEndix,
            isNext: isNext,
            accountType: "user",
          }
        });


      }, 500);
    }
else {
  setTimeout(() => {
    console.log(filterData.color);
    getProducts({
      variables: {
        color:filterData.color?filterData.color: [],
        size: filterData.discount?filterData.discount:[],
        category: filterData.ideaFor[0]?filterData.ideaFor[0]:"",
        brand: filterData.brand?filterData.brand:[],
        page: firstLoading ? 1 : page,
        minPrice: filterData.rating?filterData.rating[0]:null,
        maxPrice: filterData.rating?filterData.rating[1]:null,
        perPage: perPage,
        sortBy:  "default",
        list: true,
        lastEndix: lastEndix,
        isNext: isNext,
        accountType: "user",
      }
    });


  }, 500);
}

  }, [search ,filterData]);
  useEffect(() => {
    if (data) {
      console.log(data);
      setFirstLoading(false);
      setLastEndix(data.products.lastEndix);
      //  setProducts(data.products.data);
      // console.log();
      const datas = {
        lastVisible :data.products.lastEndix,    
list :data.products.data,  
sum :data.products.totalCount,
       
      };
      dispatch(searchFilter(datas));
setDatalist(data.products.data);
    }

  }, [data]);
 useEffect(()=>console.log(datalist));
  return (
    <>
      <AppsHeader>
        <ProductHeader
          list={datalist}
          viewType={viewType}
          page={page}
          totalProducts={sum}
          onPageChange={onPageChange}
          onSearch={searchProduct}
        />
      </AppsHeader>

      <AppsContent
        style={{ backgroundColor: alpha(theme.palette.background.default, 0.6) }}
      >
        <Box
          sx={{
            width: '100%',
            flex: 1,
            display: 'flex',
            py: 2,
            px: 4,
            height: 1,
            '& > div': {
              width: '100%',
            },
          }}
        >
          {viewType === VIEW_TYPE.GRID ? (
            <ProductGrid ecommerceList={datalist} loading={loading} />
          ) : (
            <ProductList ecommerceList={datalist} loading={loading} />
          )}
        </Box>
      </AppsContent>
      <Hidden smUp>
        {list.length > 0 ? (
          <AppsFooter>
            <AppsPagination
              count={sum}
                         rowsPerPage={40}

              page={page}
              onPageChange={onPageChange}
            />
          </AppsFooter>
        ) : null}
      </Hidden>
    </>
  );
};

export default ProductListing;
