import React from 'react';
import {alpha, Box, Button, Hidden, Stack} from '@mui/material';
import AppSearch from '../../../../@crema/core/AppSearchBar';
import ListIcon from '@mui/icons-material/List';
import AppsIcon from '@mui/icons-material/Apps';
import {useDispatch} from 'react-redux';
import {VIEW_TYPE} from '../../../../redux/reducers/Ecommerce';
import IconButton from '@mui/material/IconButton';
import PropTypes from 'prop-types';
import {setViewType} from '../../../../redux/actions';
import {styled} from '@mui/material/styles';
import clsx from 'clsx';
import {useIntl} from 'react-intl';
import AppsPagination from '../../../../@crema/core/AppsPagination';
import {useNavigate} from 'react-router-dom';

const IconBtn = styled(IconButton)(({theme}) => {

  return {
    color: theme.palette.text.disabled,
    backgroundColor: alpha(theme.palette.primary.main, 0.05),
    padding: 8,
    '&:hover, &:focus': {
      color: theme.palette.primary.main,
    },
    '&.active': {
      color: theme.palette.primary.main,
    },
  };
});
const ProductHeader = ({
  onSearch,
  viewType,
  list,
  page,
  totalProducts,
  onPageChange,
}) => {
  const dispatch = useDispatch();
  const navigation = useNavigate();
  const handleViewProduct = () => {
    navigation('/product-management/AddProductForm');
  };

  const {messages} = useIntl();
  return (
    <Box
      sx={{
        display: 'flex',
        flex: 1,
        alignItems: 'center',
      }}
    >
      <Box sx={{mr: 3}}>
        <AppSearch
          placeholder='Search here'
          onChange={(e) => onSearch(e.target.value)}
        />
      </Box>

      <Stack
        spacing={2}
        direction='row'
        sx={{
          display: 'flex',
          alignItems: 'center',
          ml: 'auto',
        }}
      >
        <IconBtn
          onClick={() => dispatch(setViewType(VIEW_TYPE.LIST))}
          className={clsx({
            active: viewType === VIEW_TYPE.LIST,
          })}
        >
          <ListIcon />
        </IconBtn>
        <IconBtn
          onClick={() => dispatch(setViewType(VIEW_TYPE.GRID))}
          className={clsx({
            active: viewType === VIEW_TYPE.GRID,
          })}
        >
          <AppsIcon />
        </IconBtn>
        <Hidden smDown>
          {list.length > 0 ? (
            <Box
              component='span'
              sx={{
                ml: {sm: 'auto'},
              }}
            >
              <AppsPagination
                           rowsPerPage={40}

                count={totalProducts}
                page={page}
                onPageChange={onPageChange}
              />
            </Box>
          ) : null}
        </Hidden>
      </Stack>
      <Box
        display='flex'
        justifyContent={'flex-end'}
        flexDirection='row'
        alignItems='left'
      >
        <Button size='small' variant='contained' color='primary' onClick={handleViewProduct}>
        {messages['ecommerce.product.addproduct']}
        </Button>
      </Box>
    </Box>
  );
};

export default ProductHeader;

ProductHeader.propTypes = {
  viewType: PropTypes.number,
  onSearch: PropTypes.func,
  list: PropTypes.array,
  page: PropTypes.number,
  totalProducts: PropTypes.number,
  onPageChange: PropTypes.func,
};
